$mdc-theme-primary: #4f93ff;
$mdc-theme-secondary: #ff8f00;
$mdc-theme-on-primary: #fff;
$mdc-theme-on-secondary: #fff;

@import "@material/top-app-bar/mdc-top-app-bar";
@import "@material/icon-button/mdc-icon-button";
@import "@material/elevation/mdc-elevation";
@import "@material/card/mdc-card";
@import "@material/select/mdc-select";
@import "@material/textfield/mdc-text-field";
@import "@material/fab/mdc-fab";
@import "@material/typography/mdc-typography";

html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    font-family: 'Roboto', sans-serif;
}

#content {
    box-sizing: border-box;
    height: 100%;
    width: 100%;
}

.mdc-top-app-bar__title {
    user-select: none;
}

.mdc-top-app-bar__row {
    color: white !important;
}

#dashboard,
#rooms,
#notifications {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    height: 100%;
    width: auto;
    // margin: 16px;
    padding: 16px;
}

.gauges {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    overflow: hidden;
    height: 100%;
}

.gaugesCard {
    flex: 1 1 1px;
    padding: 0px;
    margin-bottom: 16px;
    min-height: 100px;
}

#gaugeCard {
    display: none;
}

#output {
    margin-top: 16px;
}

.gauges * {
    flex: 1 1 1px;
}

.gaugeHeading {
    margin: 16px 16px 8px 16px;
    // text-align:center;
    user-select: none;
}

i.material-icons {
    font-size: 14px;
    padding-bottom: 3px;
}

#tempCard {
    flex: 4 1 1px;
    overflow: hidden;
    display: flex;
    align-items: stretch;
    color: #FFF;
    cursor: pointer;
}

.subsensortext {
    display: inline-flex;
    align-items: center;
}

#tempPlot {
    // height: 100%;
    // width: 100%;
    flex: 1 1 auto;
}

#plottedValueLabel {
    text-align: center;
    user-select: none;
    position: absolute;
    left: 8px;

}

#plotHeading {
    display: flex;
    flex-direction: row;
    position: relative;
    justify-content: center;
    margin-top: 8px;
    margin-bottom: 8px;
}

.dayNavigationButton {
    line-height: 19px;
    cursor: pointer;
}

#notifications>*,
#rooms>* {
    margin-top: 8px;
    margin-bottom: 8px;
}

.room {
    padding: 8px;
}

.room>* {
    margin-top: 8px;
    margin-bottom: 8px;
    display: flex;
    flex-direction: column;
}

.roomRow {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

.roomRow>label {
    margin-right: 16px;
    margin-bottom: 8px;
}

.roomRow>*:not(label) {
    flex: 1 1 auto;
}

.roomRow>div {
    padding: 0px;
    margin: 0px;
    display: flex;
    justify-content: center;
}

.mdc-fab {
    position: fixed !important;
    right: 16px;
    bottom: 16px;
    z-index: 100000;
}

//mdc-fixes
select option {
    background-color: $mdc-theme-surface;
}

.mdc-select:not(.mdc-select--disabled) {
    background-color: $mdc-theme-surface;
}

.mdc-select__native-control {
    background-color: $mdc-theme-surface;
    padding-top: 0px;
    height: auto !important;

}

.mdc-select {
    padding: 0px;
    height: auto !important;
    width: 100%;
}

.mdc-select__dropdown-icon {
    bottom: 3px;
}

.select-container {
    height: 31px;
    padding: 0px;
}



.mdc-text-field {
    background-color: rgba(0, 0, 0, 0) !important;
    height: 42px;
}

.mdc-text-field__input {
    //margin: 0px 0px 0px 16px !important;
    padding-top: 0px;
}

#home-button {
    cursor: pointer;
}

.debug-table {
    td {
        padding: 0 1em;
    }

    thead td {
        font-weight: bold;
    }
}

#loadAllLogsButton {
    margin: 16px;
}

#plotTimeframeSelection {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

#nextDay {
    visibility: hidden;
}